.countdown-container {
  background-color: black;
  color: pink;
  text-align: center;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  padding-top: 3%;
}

.countdown-text {
  font-family: 'digital-7', sans-serif;
  font-size: 16rem;
  line-height: 100%;
  margin-top: 1rem;
  margin-bottom: 1rem;
  padding: 0;
}

.textbox{
  margin: 0;
  padding: 0;
}
@font-face {
  font-family: 'digital-7';
  src: url('./fonts/digital-7\ \(mono\).ttf');
}

h1 {
font-family: Verdana, Geneva, Tahoma, sans-serif;
font-weight: 300;
}

h2 {
  font-family: 'digital-7', sans-serif;
  font-size: 9rem;
  line-height: 100%;
  margin-top: 1rem;
  margin-bottom: 1rem;
  padding: 0;
}

.inlinebox{
  display: flex;
  justify-content: center;
  align-items: baseline;
  width: 100%;
}